.section-products {
  background: url(../images/background_mobile.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @include medium {
    background: url(../images/background_tablet.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  @include large {
    background: url(../images/background_desktop.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}

.head-products {
  display: block;
  text-align: center;
  padding-top: 60px;
  padding-bottom: 40px;

  @include medium {
    padding-bottom: 80px;
  }
  @include large {
    padding-bottom: 120px;
  }
}
.head-products__discription {
  color: #131313;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 800;
  letter-spacing: 1.2px;
  text-transform: uppercase;

  @include large {
    font-size: 30px;
  }
}
.head-products__name {
  color: #131313;
  font-size: 30px;
  font-family: 'Seymour One', sans-serif;
  letter-spacing: 2.16px;
  text-transform: uppercase;
  padding-top: 12px;
}
.products {
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
  align-items: center;

  @include medium {
    flex-wrap: nowrap;
    gap: 16px;
  }
  @include large {
    gap: 29px;
    align-items: center;
  }
}
.products__card {
  border-radius: 24px;
  display: block;
  text-align: center;
  width: 100%;
  margin-bottom: 60px;

  @include medium {
    max-width: 224px;

    //   margin-top: 120px;
    //   margin-bottom: 60px;
  }
  @include large {
    max-width: 370px;
  }
}
.ice-cream__img {
  display: inline;
  border-radius: 50%;
  padding-left: 40px;
  padding-right: 40px;
  @include large {
    margin-left: 25px;
    margin-right: 25px;
  }
}
.products__card--green {
  background-color: #aec6a5;
}
.ice-cream {
  position: relative;
  z-index: 11;
}
.ice-cream::before {
  content: '';
  border-radius: 50%;
  width: 302px;
  height: 302px;
  position: absolute;
  top: -16;
  left: 0;
  box-shadow: 0 8px 30px rgba(10, 136, 23, 0.25);
  background-image: linear-gradient(rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7));
  z-index: 12;
}
.ice-coffee {
  @extend .ice-cream;
}
.ice-coffee::before {
  content: '';
  border-radius: 50%;
  width: 302px;
  height: 302px;
  position: absolute;
  top: -16;
  left: 0;
  box-shadow: 0 8px 30px rgba(153, 114, 23, 0.25);
  background-image: linear-gradient(rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7));
  z-index: 12;
}
.mikshakes {
  @extend .ice-cream;
}
.mikshakes::before {
  content: '';
  border-radius: 50%;
  width: 302px;
  height: 302px;
  position: absolute;
  top: -16;
  left: 0;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.25);
  background-image: linear-gradient(rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7));
  z-index: 12;
}
.products__card--yellow {
  background-color: #efd478;
}
.products__card--red {
  background-color: #d6936d;
}
.products__name {
  color: #131313;
  font-size: 30px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  padding-top: 66px;
  text-align: center;
}
.products__discription {
  color: #131313;
  font-size: 16px;
  font-family: 'Montserrat', sans-serif;
  line-height: 176%;
  letter-spacing: 0.64px;
  display: flex;
  text-align: center;
  padding-top: 66px;
  padding-bottom: 50px;
  padding-left: 40px;
  padding-right: 40px;
  @include small {
    padding-top: 40px;
    padding-bottom: 61px;
    padding-left: 60px;
    padding-right: 60px;
  }
  @include medium {
    padding-top: 40px;
    padding-bottom: 50px;
    padding-left: 26px;
    padding-right: 26px;
  }
  @include large {
    padding-left: 64px;
    padding-right: 64px;
  }
}
